
























import uniSegmentedControl from '@/components/uni-segmented-control.vue';
import collectionList from '@/components/collectionList.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
var userLoginInfo;
export default {
	components: {
		uniSegmentedControl,
		collectionList,
		uniLoadMore
	},
	data() {
		return {
			isCheckAll: false,
			loadingType: 1,
			showLoadMore: false,
			items: [{
					name: '宝贝收藏',
					type: 1
				},
				{
					name: '内容收藏',
					type: 0
				}
			],
			current: 0,
			goodsList: [],
			type: 1,
			page: 1,
			uid: 0
		}
	},
	onShow() {
		this.getLikeGoodsList();
	},
	onLoad() {
		userLoginInfo = this.userLoginInfo('../collection/collection_management', 1);
		this.uid = userLoginInfo.id;

		this.goodsList = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
		// this.getLikeGoodsList();
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		//console.log("onReachBottom");
		this.getLikeGoodsList();
	},
	
	methods: {
		goBack(){
			uni.navigateBack({
				delta: 1
			});
		},
		onClickItem(e) {
			// console.log(e);
			if (this.current !== e.index) {
				this.current = e.index;
				this.type = e.type;
				// 清空商品信息
				this.goodsList = [];
				this.page = 1;
				this.getLikeGoodsList();
			}
		},
		getLikeGoodsList: function() {
			uni.request({
				url: this.apiUrl + 'user/getCollect',
				method: "GET",
				data: {
					uid: userLoginInfo.id,
					type: this.type,
					page: this.page
				},
				success: (res) => {
					if (res.data.code === 0) {
						// console.log(this.goodsList = res.data.data);
						if (Array.isArray(res.data.data) && res.data.data.length === 0) {
							this.loadingType = 2;
							return;
						}
						if (res.data.data.length < 10) {
							this.loadingType = 2;
						}
						// 拼接加载更多数据
						this.showLoadMore = true;
						this.page++;
						let collectionData = [];
						res.data.data.forEach((orders) => {
							collectionData.push(orders);
						});
						// 拼接加载更多数据
						this.goodsList = this.goodsList.concat(collectionData);
					}
				}
			})
		},
		// 全选
		allCheck() {
			this.isCheckAll = !this.isCheckAll;
			this.isCheckAll ?
				this.shopData.forEach((item) => {
					this._shopTrue(item)
				}) :
				this.shopData.forEach((item) => {
					this._shopFalse(item)
				})
		},

	}
}
