
























	var userLoginInfo;
	export default {
		name: "collectionList",
		props: {
			goodsList: Array,
			type: Number,
			uid: Number
		},
		data() {
			return {
				isCheckAll: false,
				checkedCount: 0,
			};
		},
		methods: {
			// 全选
			allCheck() {
				this.isCheckAll = !this.isCheckAll;
				this.isCheckAll ?
					this.goodsList.forEach((proItem) => {
						this._checkTrue(proItem)
					}) :
					this.goodsList.forEach((proItem) => {
						this._checkFalse(proItem)
					})
			},
			_checkTrue(item) {
				item.isChecked = true;
				++this.checkedCount == this.goodsList.length ? this.isCheckAll = true : this.isCheckAll = false
			},
			_checkFalse(item) {
				item.isChecked = false;
				--this.checkedCount;
				this.isCheckAll = false;
			},
			onLoad: function() {
				//userLoginInfo = this.userLoginInfo('../addAdress/adress', 1);
				//console.log(userLoginInfo);
			},
			// 删除商品或者文章
			getCheckData: function(type) {
				let ids = [];
				this.goodsList.forEach((proItem) => {
					if (proItem.isChecked) {
						ids.push(proItem.id)
					}
				})
				console.log(this.type);
				console.log(ids);
				console.log(this.uid);
				//执行删除操作
				uni.request({
					url: this.apiUrl + 'user/delCollect',
					method: 'GET',
					data: {
						uid: this.uid,
						ids: ids,
						type: this.type
					},
					success: (res) => {
						console.log(res.data);
						if (res.data.code == 0) {
// 							uni.showToast({
// 								title: '删除成功！',
// 								icon: 'none',
// 								success: () => {
// 									// 清空商品信息
// 									this.goodsList = [];
// 									// this.$emit('onClickItem'，this.page = 1);	
// 								}
// 							})
						}
					}
				})
			}
		}
	}
